	module.exports = {"data":{"wordpressPage":{"title":"Ultrasound findings at follow-up","content":"<h2>Recommendation:</h2>\n<ul>\n<li>If clinicians choose to use ultrasound for medical abortion follow-up, the only ultrasound finding that requires intervention is an ongoing viable pregnancy.</li>\n</ul>\n<p>&nbsp;</p>\n<h3>Strength of recommendation: Strong</h3>\n<h3></h3>\n<h3>Quality of evidence: Low</h3>\n<p>Background</p>\n<p>Ultrasound is not necessary to provide abortion care (World Health Organization (WHO), 2014) but may be common in some settings. Ultrasound for follow-up after medical abortion has diagnostic limitations. Except for the rare case of an ongoing viable pregnancy, intervention after a medical abortion should be based on clinical symptoms and not ultrasound findings.</p>\n<p>Ultrasound findings at follow-up</p>\n<p><em>Endometrial thickening</em>: After a successful medical abortion, endometrial thickness varies and can be associated with a complex or heterogeneous appearance.</p>\n<p>INSERT IMAGE HERE</p>\n<p>CAPTION: <em>Endometrial thickening</em></p>\n<p>PHOTO CREDIT: <em>Courtesy of Mary Fjerstad</em></p>\n<p>Multiple retrospective and prospective cohort studies have shown that endometrial thickness has a wide range in women after medical abortion, with significant overlap between women with successful and failed medical abortion (Cowett, Cohen, Lichtenberg, &amp; Stika, 2004; Markovitch, Tepper, Klein, Fishman, &amp; Aviram, 2006; Parashar, Iversen, Midbøe, Myking, &amp; Bjørge, 2007; Rørbye, Nørgaard, &amp; Nilas, 2004; Tzeng, Hwang, Au, &amp; Chien, 2013). In a pooled analysis of 2,208 women one week after medical abortion, after women with a persistent gestational sac were excluded, the average endometrial thickness was 10.9mm in women who did not require more intervention and 14.5mm in 30 women who did require intervention (Reeves, Fox, Lohr, &amp; Creinin, 2009). Although the average endometrial thickness in women who require intervention tends to be higher, because of the range and overlap between successful and unsuccessful abortion, no study has found that there is a thickness above which a diagnosis of unsuccessful medical abortion can be made. The decision to intervene should be made on clinical signs and symptoms, such as ongoing or heavy bleeding, rather than on ultrasound findings.</p>\n<p><em>Persistent gestational sac:</em> A persistent gestational sac, in which the sac is present but there is no viable embryonic tissue, occurs in less than 1% of medical abortions with the recommended mifepristone and misoprostol regimen (Creinin et al., 2004; Creinin et al., 2007; Winikoff et al., 2008). A persistent gestational sac is not a viable pregnancy and may be managed with aspiration, a second dose of misoprostol or expectant management according to a woman’s preference. In a study of women with a persistent gestational sac within 11 days of medical abortion, a second dose of misoprostol was found to lead to expulsion of a nonviable sac in 69% of women (Reeves, Kudva, &amp; Creinin, 2008).</p>\n<p>INSERT IMAGE HERE</p>\n<p>CAPTION: <em>Persistent gestational sac</em></p>\n<p>PHOTO CREDIT: <em>Courtesy of Mary Fjerstad</em></p>\n<p><em>Ongoing viable pregnancy:</em> An ongoing pregnancy, in which a growing sac and/or embryo with cardiac activity are present, occurs in less than 1% of medical abortions with the recommended mifepristone and misoprostol regimen (Von Hertzen et al., 2009; Winikoff et al., 2008). Some women will be able to identify this outcome without ultrasound due to lack of bleeding or continued pregnancy symptoms. A woman with an ongoing pregnancy should be offered uterine evacuation as soon as possible with either vacuum aspiration or a second dose of misoprostol, depending on gestational age and local context. The success rate of misoprostol after failed medical abortion is 36% (Reeves et al., 2008; WHO, 2014). If a woman chooses a second dose of misoprostol, she must be followed to see if it is successful.</p>\n<h3>References</h3>\n<p>Cowett, A. A., Cohen, L. S., Lichtenberg, E. S., &amp; Stika, C. S. (2004). Ultrasound evaluation of the endometrium after medical termination of pregnancy. <em>Obstetrics &amp; Gynecology,</em> <em>103</em>(5, Part 1), 871-875.</p>\n<p>&nbsp;</p>\n<p>Creinin, M. D., Fox, M. C., Teal, S., Chen, A., Schaff, E. A., &amp; Meyn, L. A. (2004). A randomized comparison of misoprostol 6 to 8 hours versus 24 hours after mifepristone for abortion. <em>Obstetrics &amp; Gynecology</em>, <em>103</em>(5, Part 1), 851-859.</p>\n<p>&nbsp;</p>\n<p>Creinin, M. D., Schreiber, C. A., Bednarek, P., Lintu, H., Wagner, M. S., &amp; Meyn, L. A. (2007). Mifepristone and misoprostol administered simultaneously versus 24 hours apart for abortion: A randomized controlled trial. <em>Obstetrics &amp; Gynecology, 109</em>(4), 885-894.</p>\n<p>&nbsp;</p>\n<p>Markovitch, O., Tepper, R., Klein, Z., Fishman, A., &amp; Aviram, R. (2006). Sonographic appearance of the uterine cavity following administration of mifepristone and misoprostol for termination of pregnancy. <em>Journal of Clinical Ultrasound</em>, <em>34</em>(6), 278-282.</p>\n<p>&nbsp;</p>\n<p>Parashar, P., Iversen, O. E., Midbøe, G., Myking, O., &amp; Bjørge, L. (2007). Medical abortion in the first trimester: The use of serum hCG and endometrial thickness as markers of completeness. <em>European Journal of Contraception and Reproductive Health Care</em>, <em>12</em>(4), 366-371.</p>\n<p>&nbsp;</p>\n<p>Reeves, M., Fox, M., Lohr, P., &amp; Creinin, M. (2009). Endometrial thickness following medical abortion is not predictive of subsequent surgical intervention. <em>Ultrasound in Obstetrics &amp; Gynecology</em>, <em>34</em>(1), 104-109.</p>\n<p>&nbsp;</p>\n<p>Reeves, M. F., Kudva, A., &amp; Creinin, M. D. (2008). Medical abortion outcomes after a second dose of misoprostol for persistent gestational sac. <em>Contraception</em>, <em>78</em>(4), 332-335.</p>\n<p>&nbsp;</p>\n<p>Rørbye, C., Nørgaard, M., &amp; Nilas, L. (2004). Prediction of late failure after medical abortion from serial β‐hCG measurements and ultrasonography. <em>Human Reproduction</em>, <em>19</em>(1), 85-89.</p>\n<p>&nbsp;</p>\n<p>Tzeng, C.R., Hwang, J.L., Au, H.K., &amp; Chien, L.W. (2013). Sonographic patterns of the endometrium in assessment of medical abortion outcomes. <em>Contraception</em>, <em>88</em>(1), 153-9.</p>\n<p>&nbsp;</p>\n<p>Von Hertzen, H., Piaggio, G., Wojdyla, D., Marions, L., My Huong, N., Tang, O., &amp; Mittal, S. (2009). Two mifepristone doses and two intervals of misoprostol administration for termination of early pregnancy: A randomised factorial controlled equivalence trial. <em>BJOG: An International Journal of Obstetrics &amp; Gynaecology</em>, <em>116</em>(3), 381-389.</p>\n<p>&nbsp;</p>\n<p>Winikoff, B., Dzuba, I. G., Creinin, M. D., Crowden, W. A., Goldberg, A. B., Gonzales, J., . . . Shannon, C. S. (2008). Two distinct oral routes of misoprostol in mifepristone medical abortion: A randomized controlled trial. <em>Obstetrics &amp; Gynecology</em>, <em>112</em>(6), 1303-1310.</p>\n<p>&nbsp;</p>\n<p>World Health Organization. (2014). <em>Clinical practice handbook for safe abortion</em>. Geneva: World Health Organization Press.</p>\n","fields":{"path":"before-13/ma/ultrasound-findings"},"acf":{"last_reviewed":"20171031","landing_page":false}},"parent":{"title":"Medical abortion","fields":{"path":"before-13/ma"},"wordpress_parent":163},"grandparent":{"title":"Recommendations for abortion before 13 weeks gestation","fields":{"path":"before-13"}},"site":{"id":"Site","siteMetadata":{"title":"Ipas Clinical Updates"}}},"pathContext":{"id":"f7216b84-3749-543a-8476-a10e33de71de","title":"Ultrasound findings at follow-up","content":"<h2>Recommendation:</h2>\n<ul>\n<li>If clinicians choose to use ultrasound for medical abortion follow-up, the only ultrasound finding that requires intervention is an ongoing viable pregnancy.</li>\n</ul>\n<p>&nbsp;</p>\n<h3>Strength of recommendation: Strong</h3>\n<h3></h3>\n<h3>Quality of evidence: Low</h3>\n<p>Background</p>\n<p>Ultrasound is not necessary to provide abortion care (World Health Organization (WHO), 2014) but may be common in some settings. Ultrasound for follow-up after medical abortion has diagnostic limitations. Except for the rare case of an ongoing viable pregnancy, intervention after a medical abortion should be based on clinical symptoms and not ultrasound findings.</p>\n<p>Ultrasound findings at follow-up</p>\n<p><em>Endometrial thickening</em>: After a successful medical abortion, endometrial thickness varies and can be associated with a complex or heterogeneous appearance.</p>\n<p>INSERT IMAGE HERE</p>\n<p>CAPTION: <em>Endometrial thickening</em></p>\n<p>PHOTO CREDIT: <em>Courtesy of Mary Fjerstad</em></p>\n<p>Multiple retrospective and prospective cohort studies have shown that endometrial thickness has a wide range in women after medical abortion, with significant overlap between women with successful and failed medical abortion (Cowett, Cohen, Lichtenberg, &amp; Stika, 2004; Markovitch, Tepper, Klein, Fishman, &amp; Aviram, 2006; Parashar, Iversen, Midbøe, Myking, &amp; Bjørge, 2007; Rørbye, Nørgaard, &amp; Nilas, 2004; Tzeng, Hwang, Au, &amp; Chien, 2013). In a pooled analysis of 2,208 women one week after medical abortion, after women with a persistent gestational sac were excluded, the average endometrial thickness was 10.9mm in women who did not require more intervention and 14.5mm in 30 women who did require intervention (Reeves, Fox, Lohr, &amp; Creinin, 2009). Although the average endometrial thickness in women who require intervention tends to be higher, because of the range and overlap between successful and unsuccessful abortion, no study has found that there is a thickness above which a diagnosis of unsuccessful medical abortion can be made. The decision to intervene should be made on clinical signs and symptoms, such as ongoing or heavy bleeding, rather than on ultrasound findings.</p>\n<p><em>Persistent gestational sac:</em> A persistent gestational sac, in which the sac is present but there is no viable embryonic tissue, occurs in less than 1% of medical abortions with the recommended mifepristone and misoprostol regimen (Creinin et al., 2004; Creinin et al., 2007; Winikoff et al., 2008). A persistent gestational sac is not a viable pregnancy and may be managed with aspiration, a second dose of misoprostol or expectant management according to a woman’s preference. In a study of women with a persistent gestational sac within 11 days of medical abortion, a second dose of misoprostol was found to lead to expulsion of a nonviable sac in 69% of women (Reeves, Kudva, &amp; Creinin, 2008).</p>\n<p>INSERT IMAGE HERE</p>\n<p>CAPTION: <em>Persistent gestational sac</em></p>\n<p>PHOTO CREDIT: <em>Courtesy of Mary Fjerstad</em></p>\n<p><em>Ongoing viable pregnancy:</em> An ongoing pregnancy, in which a growing sac and/or embryo with cardiac activity are present, occurs in less than 1% of medical abortions with the recommended mifepristone and misoprostol regimen (Von Hertzen et al., 2009; Winikoff et al., 2008). Some women will be able to identify this outcome without ultrasound due to lack of bleeding or continued pregnancy symptoms. A woman with an ongoing pregnancy should be offered uterine evacuation as soon as possible with either vacuum aspiration or a second dose of misoprostol, depending on gestational age and local context. The success rate of misoprostol after failed medical abortion is 36% (Reeves et al., 2008; WHO, 2014). If a woman chooses a second dose of misoprostol, she must be followed to see if it is successful.</p>\n<h3>References</h3>\n<p>Cowett, A. A., Cohen, L. S., Lichtenberg, E. S., &amp; Stika, C. S. (2004). Ultrasound evaluation of the endometrium after medical termination of pregnancy. <em>Obstetrics &amp; Gynecology,</em> <em>103</em>(5, Part 1), 871-875.</p>\n<p>&nbsp;</p>\n<p>Creinin, M. D., Fox, M. C., Teal, S., Chen, A., Schaff, E. A., &amp; Meyn, L. A. (2004). A randomized comparison of misoprostol 6 to 8 hours versus 24 hours after mifepristone for abortion. <em>Obstetrics &amp; Gynecology</em>, <em>103</em>(5, Part 1), 851-859.</p>\n<p>&nbsp;</p>\n<p>Creinin, M. D., Schreiber, C. A., Bednarek, P., Lintu, H., Wagner, M. S., &amp; Meyn, L. A. (2007). Mifepristone and misoprostol administered simultaneously versus 24 hours apart for abortion: A randomized controlled trial. <em>Obstetrics &amp; Gynecology, 109</em>(4), 885-894.</p>\n<p>&nbsp;</p>\n<p>Markovitch, O., Tepper, R., Klein, Z., Fishman, A., &amp; Aviram, R. (2006). Sonographic appearance of the uterine cavity following administration of mifepristone and misoprostol for termination of pregnancy. <em>Journal of Clinical Ultrasound</em>, <em>34</em>(6), 278-282.</p>\n<p>&nbsp;</p>\n<p>Parashar, P., Iversen, O. E., Midbøe, G., Myking, O., &amp; Bjørge, L. (2007). Medical abortion in the first trimester: The use of serum hCG and endometrial thickness as markers of completeness. <em>European Journal of Contraception and Reproductive Health Care</em>, <em>12</em>(4), 366-371.</p>\n<p>&nbsp;</p>\n<p>Reeves, M., Fox, M., Lohr, P., &amp; Creinin, M. (2009). Endometrial thickness following medical abortion is not predictive of subsequent surgical intervention. <em>Ultrasound in Obstetrics &amp; Gynecology</em>, <em>34</em>(1), 104-109.</p>\n<p>&nbsp;</p>\n<p>Reeves, M. F., Kudva, A., &amp; Creinin, M. D. (2008). Medical abortion outcomes after a second dose of misoprostol for persistent gestational sac. <em>Contraception</em>, <em>78</em>(4), 332-335.</p>\n<p>&nbsp;</p>\n<p>Rørbye, C., Nørgaard, M., &amp; Nilas, L. (2004). Prediction of late failure after medical abortion from serial β‐hCG measurements and ultrasonography. <em>Human Reproduction</em>, <em>19</em>(1), 85-89.</p>\n<p>&nbsp;</p>\n<p>Tzeng, C.R., Hwang, J.L., Au, H.K., &amp; Chien, L.W. (2013). Sonographic patterns of the endometrium in assessment of medical abortion outcomes. <em>Contraception</em>, <em>88</em>(1), 153-9.</p>\n<p>&nbsp;</p>\n<p>Von Hertzen, H., Piaggio, G., Wojdyla, D., Marions, L., My Huong, N., Tang, O., &amp; Mittal, S. (2009). Two mifepristone doses and two intervals of misoprostol administration for termination of early pregnancy: A randomised factorial controlled equivalence trial. <em>BJOG: An International Journal of Obstetrics &amp; Gynaecology</em>, <em>116</em>(3), 381-389.</p>\n<p>&nbsp;</p>\n<p>Winikoff, B., Dzuba, I. G., Creinin, M. D., Crowden, W. A., Goldberg, A. B., Gonzales, J., . . . Shannon, C. S. (2008). Two distinct oral routes of misoprostol in mifepristone medical abortion: A randomized controlled trial. <em>Obstetrics &amp; Gynecology</em>, <em>112</em>(6), 1303-1310.</p>\n<p>&nbsp;</p>\n<p>World Health Organization. (2014). <em>Clinical practice handbook for safe abortion</em>. Geneva: World Health Organization Press.</p>\n","last_reviewed":"20171031","parent":185,"grandparent":163}}

